import React from 'react';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
} from 'chart.js';
import axios from 'axios';
import { useState, useEffect } from 'react';
import { CryptoState } from '../../Pages/CryptoContext/CryptoContext';
import { HistoricalChart } from '../../config/api';
import { createTheme, ThemeProvider, makeStyles, CircularProgress } from '@material-ui/core';
import { Line } from 'react-chartjs-2';
import { chartDays } from './../../config/data';
import SelectButton from './../SelectButton/SelectButton';

ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend
);



const useStyles = makeStyles((theme) => ({

    container: {
        width: '100%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        marginTop: 25,
        padding: 40,

        [theme.breakpoints.down("md")]: {
            width: '100%',
            marginTop: 0,
            padding: 20,
            paddingTop: 0
        }
    }

}));

function CoinInfo({ coin }) {

    const [historicalData, setHistoricalData] = useState();
    const [days, setDays] = useState(1);




    const { currency } = CryptoState();

    const classes = useStyles();

    const fetchHistoricalData = async () => {
        const { data } = await axios.get(HistoricalChart(coin.id, currency, days));



        setHistoricalData(data.prices);
    };


    useEffect(() => {
        fetchHistoricalData();
    }, [currency, days]);



    return (
       
            <div className={classes.container}>
                {
                    !historicalData ? (
                        <CircularProgress
                            style={{ color: "gold" }}
                            size={250}
                            thickness={1}
                        />
                    ) : (<>
                        <Line data={{
                            labels: historicalData.map((coin) => {
                                let date = new Date(coin[0]);
                                let time = date.getHours() > 12
                                    ? `${date.getHours() - 12}: ${date.getMinutes()} PM`
                                    : `${date.getHours()}: ${date.getMinutes()} PM`

                                return days === 1 ? time : date.toLocaleDateString();
                            }),
                            datasets: [{
                                data: historicalData.map((coin) => coin[1]),
                                label: `price ( Past ${days} Days ) in ${currency}`,
                                borderColor: "rgb(78, 255, 24)"
                            },],
                        }}
                            options={{
                                elements: {
                                    point: {
                                        radius: 1
                                    },
                                },
                            }}
                        />
                        <div style={{
                            display: "flex",
                            marginTop: 20,
                            justifyContent: "space-around",
                            width: "100%",
                        }}>
                            {chartDays.map((day) => (
                                <SelectButton
                                    key={day.value}
                                    onClick={() => {
                                        setDays(day.value)
                                    }}
                                    selected={day.value === days}
                                >{day.label}</SelectButton>
                            ))}
                        </div>
                    </>)
                }
            </div>
  
    )
}

export default CoinInfo
